/***************************************
Aione Blog
***************************************/

/************************************
	List Blog Page
************************************/

.blog .post-navigation{
	display: none;
}


/************************************************
List Blog Design
************************************************/
.list-blog{
	padding-right: 20px;
	margin-bottom: 40px;
}
.list-blog .entry-header .entry-title{
	margin: 0 0 10px 0;
}
.list-blog .entry-header .entry-title a{
	color: #757575;
}
.list-blog .entry-header .entry-meta{
	color: #a9a9a9;
	font-size: 16px;
}
.list-blog .entry-content{
	font-size:16px;
	line-height: 1.3;
	margin: 0 0 10px 0;
}
.list-blog .entry-footer{
	padding: 0;
	border:none;
}
.list-blog .entry-footer .read-more-link{
    border: 1px solid #44b0ed;
    display: inline-block;
    padding: 10px 30px 10px 10px;
	position: relative;
}
.list-blog .read-more-link:after{
	right: 10px;
	content: "\f3d1";
	line-height: 17px;
	position: absolute;
	font-family:'Ionicons'; 
}











/***************************************
Aione Single
***************************************/
.featured-image {
	overflow:hidden; 
	margin: 0 0 20px 0;
}
.featured-image img{
	width:100%;
	height:auto;
}
.entry-header .entry-title {
    font-size: 30px;
    margin: 0 0 20px 0;
}
.entry-header .entry-meta{
    font-size: 18px;
    line-height: 20px;
    margin: 0 0 10px 0;
}
.entry-content{
	margin: 0 0 20px 0;
}
.entry-footer{
	margin: 0 0 10px 0;
	border: 1px solid #e8e8e8;
    padding: 10px;
}
.entry-footer::after{
	content: '';
	display: table;
	clear: both;
}
.entry-footer .edit-link{
	padding-left: 10px;
}







.navigation {
    border: 1px solid #e8e8e8;
    padding: 10px;
    margin: 0 0 20px 0;
}
.navigation::after{
	content: '';
	display: table;
	clear: both;
}
.navigation .screen-reader-text{
	display: none;
}
.navigation .nav-links .nav-next,
.navigation .nav-links .nav-previous{

}
.navigation .nav-links .nav-next{
	float: right;
}
.navigation .nav-links .nav-previous{
	float: left;
}
.navigation .nav-links .nav-next a,
.navigation .nav-links .nav-previous a{
	font-size: 18px;
    line-height: 30px;
    padding: 0;
    color: #ffffff;
    background: #168dc5;
    display: inline-block;
}
.navigation .nav-links .nav-next a{
	padding-left: 10px;
}
.navigation .nav-links .nav-previous a{
	padding-right: 10px;
}
.navigation .nav-links .nav-next a:hover,
.navigation .nav-links .nav-previous a:hover{
    background: #1570a6;
}
.navigation .nav-links .nav-next > a >i,
.navigation .nav-links .nav-previous > a > i{
	display: inline-block;
	padding: 0 10px;
}