/***************************************
Aione Buttons
***************************************/
.aione-button{
    border: none;
    color: #FFFFFF;
    font-size: 1.125em;
    line-height: 1.5;
    font-weight: normal;
    margin: 0 0 5px 0;
    padding: 0.5em 1em;
    cursor: pointer;
    display: inline-block;
    background-color: #1c2225;
    @include border-radius( 3px );
    @include transition( all 180ms ease-in-out );    
}
.aione-button:hover{
    background-color: #191e21;
}
.aione-button.small{
    font-size:1em;
    padding: 0.3125em 0.625em;
}
.aione-button.medium{
    font-size:1.25em;
    padding: 0.625em 0.875em;
}
.aione-button.large{
    font-size:1.5em;
    padding: 0.875em 1.125em;
}
.aione-button.wide{
    padding-left:1.875em;
    padding-right:1.875em;
}
.aione-button.fullwidth{
    width: 100%;
    display: block;
    -moz-box-sizing:border-box;
    -webkit-box-sizing:border-box;
    box-sizing:border-box;
}
.aione-button.square{
    @include border-radius( 0 );           
}
.aione-button.rounded{
    @include border-radius( 4px );
}
.aione-button.rounded.small{
    @include border-radius( 4px );
}
.aione-button.rounded.medium{
    @include border-radius( 6px );
}
.aione-button.rounded.large{
    @include border-radius( 10px );
}
.aione-button.circle{
    @include border-radius( 100px );
}
.outline{
    background-color: transparent;
    color: #1c2225;
    border: 2px solid #1c2225;
}
.aione-button.light,
.aione-button.light:hover{
    color: #333333;
    background-color: #FFFFFF;
    border-color:#FFFFFF;
}
.aione-button.light.outline,
.aione-button.light.outline:hover{
    color: #454545;
    background-color: #FFFFFF;
    border-color:#FFFFFF;
}
.aione-button.dark,
.aione-button.dark:hover{
    color: #FFFFFF;
    background-color: #454545;
}
.aione-button.dark.outline,
.aione-button.dark.outline:hover{
    color: #454545;
    background-color: transparent;
    border-color:#454545;
}
.aione-button.primary{
    color: #FFFFFF;
    background-color: #168DC5;
}
.aione-button.primary:hover{
    color: #FFFFFF;
    background-color: #1570A6;
}
.aione-button.primary.outline{
    background-color: transparent;
    color: #168DC5;
    border-color: #168DC5;
}
.aione-button.primary.outline:hover{
    background-color: transparent;
    color: #1570A6;
    border-color: #1570A6;
}