/*************************************
Aione Grid
***************************************/
$aione-columns:100;
$i: 1;
@while $i <= 20 {
  $perc: unquote((100 / $i ) + "%");
  .ar.columns-#{$i} .ac,
  .ar.s-columns-#{$i} .ac {
    width: $perc;
  }
  $i: $i + 1;
}
@media #{$medium-and-up} {
  $i: 1;
  @while $i <= 20 {
    $perc: unquote((100 / $i ) + "%");
    .ar.m-columns-#{$i} .ac {
      width: $perc;
    }
    $i: $i + 1;
  }
}
@media #{$large-and-up} {
   $i: 1;
   @while $i <= 20 {
    $perc: unquote((100 / $i ) + "%");
    .ar.l-columns-#{$i} .ac {
      width: $perc;
    }
    $i: $i + 1;
  }   
}
.ar.columns-1 .ac{ float: none; }
.ar{

  display:block;
  padding:0;
  margin: 0;
  // Clear floating children
  &:after {
    content: "";
    display: table;
    clear: both;
  }
  .ac{
    float: left;
    display: block;
    box-sizing: border-box;
    min-height: 1px;
    vertical-align: top;
    margin-left: auto;
    left: auto;
    right: auto;

    &[class*="push-"],
    &[class*="pull-"] {
      position: relative;
    }

    $i: 1;
    @while $i <= $aione-columns {
      $perc: unquote((100 / ($aione-columns / $i) ) + "%");
      &.s#{$i} {
        width: $perc;
      }
      $i: $i + 1;
    }

    @media #{$medium-and-up} {
      $i: 1;
      @while $i <= $aione-columns {
        $perc: unquote((100 / ($aione-columns / $i)) + "%");
        &.m#{$i} {
          width: $perc;
        }
        $i: $i + 1
      }
    }

    @media #{$large-and-up} {
      $i: 1;
      @while $i <= $aione-columns {
        $perc: unquote((100 / ($aione-columns / $i)) + "%");
        &.l#{$i} {
          width: $perc;
        }
        $i: $i + 1;
      }
    }
  }
}

.ac{
  padding: 0 $gutter-width / 2;
}
.last,
.last-column {
  padding-right: 0;
  margin-right: 0;
}

.last:after {
	content: "";
	display: table;
	clear: both;
}