/***************************************
Aione Accordion
***************************************/
.aione-accordion,
.aione-collapsible{
    
}
.aione-accordion .aione-item{
    border: 1px solid #e8e8e8;
    border-bottom:none;
}
.aione-collapsible .aione-item{
    margin-bottom: 10px;
    border: 1px solid #e8e8e8;
}
.aione-item-header{
    padding: 15px 15px 15px 45px;
    color: #666666;
    background-color: #fafafa;
    font-size: 18px;
    line-height: 1.4;
    font-weight: 100;
    font-family: "Open Sans",Arial,sans-serif;
}
.aione-accordion .aione-item .aione-item-header,
.aione-collapsible .aione-item .aione-item-header {
    display: block;
    position:relative;
    cursor: pointer;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.aione-accordion .aione-item.active .aione-item-header,
.aione-collapsible .aione-item.active .aione-item-header {
    background-color: #f2f2f2;
}
.aione-accordion .aione-item .aione-item-header:before,
.aione-collapsible .aione-item .aione-item-header:before{
    content: "";
    width: 15px;
    height: 0;
    position: absolute;
    top: 50%;
    left: 15px;
    margin-top: -1px;
    border-top: 1px solid #989898;
}
.aione-accordion .aione-item .aione-item-header:after,
.aione-collapsible .aione-item .aione-item-header:after {
    content: "";
    width: 0;
    height: 15px;
    position: absolute;
    top: 50%;
    left: 22px;
    margin-top: -8px;
    border-left: 1px solid #989898;
    -moz-transition: all 200ms ease-in-out;
    -webkit-transition: all 200ms ease-in-out;
    transition: all 200ms ease-in-out;
}
.aione-accordion .aione-item.active .aione-item-header:before,
.aione-collapsible .aione-item.active .aione-item-header:before {
}
.aione-accordion .aione-item.active .aione-item-header:after,
.aione-collapsible .aione-item.active .aione-item-header:after {
    height:0;
    margin-top: -1px;
}
.aione-item-content{
    padding: 20px;
}
.aione-accordion .aione-item .aione-item-content,
.aione-collapsible .aione-item .aione-item-content{
    display: none;
    color: #666666;
    font-size: 16px;
    line-height: 1.4;
    border-top: 1px solid #e8e8e8;
    font-family: "Open Sans",Arial,sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.aione-accordion .aione-item.active .aione-item-content,
.aione-collapsible .aione-item.active .aione-item-content{
    display:block;
}

/***************************************
Aione Message
***************************************/
.aione-message{
    padding: 20px;
    margin: 10px 0;
    font-size: 20px;
    line-height: 1.4;
    font-weight: normal;
    font-family: "Open Sans", Arial,sans-serif;
    text-align: center;
    color: #454545;
    background-color: #f2f2f2;
    border: 1px solid #e8e8e8;
}
.aione-message.success{
    color: #4caf50;
    background-color: #e8f5e9;
    border-color: #4caf50;
}
.aione-message.warning{
    color: #FF9800;
    background-color: #fff4e5;
    border-color: #FF9800;
} 
.aione-message.error{
    color: #F44336;
    background-color: #fef2f1;
    border-color: #F44336;
}

.aione-messages{

}
.aione-messages li{
    font-size: 16px;
    text-align: left;
    border-bottom: 1px dotted #e8e8e8;
    padding-bottom: 4px;
    margin-bottom: 4px;
}
.aione-message.success .aione-messages li{
    border-bottom: 1px dotted rgba(76, 175, 80, 0.2);
}
.aione-message.warning .aione-messages li{
    border-bottom: 1px dotted rgba(255, 152, 0, 0.2);
}
.aione-message.error .aione-messages li{
    border-bottom: 1px dotted rgba(244, 67, 54, 0.2);
} 



