/***************************************
Aione Tooltip
***************************************/
.aione-tooltip{
    position:relative;
}
.aione-tooltip:before{
    content: attr(title);
    height:1px;
    overflow: hidden;
    opacity: 0;
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    border-radius: 4px;
    margin: 0;
    padding: 5px 10px;
    background-color: rgba(0,0,0,.8);
    color: #ffffff;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4; 
    -webkit-transition: all .1s ease-out;
    -moz-transition: all .1s ease-out;
    transition: all .1s ease-out;
    -webkit-transform: translate(0,-100%);
    -moz-transform: translate(0,-100%);
    transform: translate(0,-100%); 

}
.aione-tooltip:hover:before{
    opacity:1; 
    height:auto; 
}




.aione-sort-button{
    cursor: pointer;
    position: relative;
}
.aione-sort-button.asc:after{
    content: "";
    border-top: 5px solid #d2d2d2;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    width: 0;
    height: 0;
    line-height: 0;
    display: inline;
    margin-left: 5px;
    position: absolute;
    top: 50%;
}
.aione-sort-button.desc:after{
    content: "";
    border-bottom: 5px solid #d2d2d2;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    width: 0;
    height: 0;
    line-height: 0;
    display: inline;
    margin-left: 5px;
    position: absolute;
    top: 50%; 
}





