/***********************************
Mixins
***********************************/

$rounded-border-radius-default: 0.3em !default;

$box-sharow-top-default: 0em !default;
$box-sharow-left-default: 0em !default;
$box-sharow-blur-default: 0.75em !default;
$box-sharow-color-default: rgba(0,0,0,0.2) !default;
$box-sharow-spread-default: -0.25px !default;

//Transition
@mixin transition($x...){
  -webkit-transition: $x;
  -moz-transition: $x;
  -ms-transition: $x;
  -o-transition: $x;
  transition: $x;
}

//Border Radius
@mixin border-radius($x...){
  -webkit-border-radius: $x;
  -moz-border-radius: $x;
  border-radius: $x;
}

//Rounded
@mixin shadow($top: $box-sharow-top-default, $left:$box-sharow-left-default, $blur:$box-sharow-blur-default, $spread:$box-sharow-spread-default, $color:$box-sharow-color-default, $inset: false) {
  @if $inset {
    -webkit-box-shadow:inset $top $left $blur $spread $color;
    -moz-box-shadow:inset $top $left $blur $spread $color;
    box-shadow:inset $top $left $blur $spread $color;
  } @else {
    -webkit-box-shadow: $top $left $blur $spread $color;
    -moz-box-shadow: $top $left $blur $spread $color;
    box-shadow: $top $left $blur $spread $color;
  }
}

//Rounded
@mixin rounded( $radius: $rounded-border-radius-default ) {
  -webkit-border-radius: $radius; 
  -moz-border-radius: $radius;
  border-radius: $radius;
}



@mixin aione-gradient($direction:45deg, $color:#ffffff, $colors...) {

  background-color: $color;
  background: nth(nth($colors, 1), 1);
  background-image: -moz-linear-gradient($direction, $colors);
  background-image: -webkit-gradient(linear,$direction, $colors);
  background-image: -webkit-linear-gradient($direction, $colors);
  background-image: -o-linear-gradient($direction, $colors);
  background-image: -ms-linear-gradient($direction, $colors);
  background-image: linear-gradient($direction, $colors);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr= #{nth(nth($colors, 1), 1)}, endColorstr= #{nth(nth($colors, -1), 1)},GradientType=1 );
}