/*************************************
aione Spacing
***************************************/
$aione-columns:100;
$i: 0;
@while $i <= $aione-columns {
  .m-#{$i} {
    margin: $i + px;
  }
  .mt-#{$i} {
    margin-top: $i + px;
  }
  .mr-#{$i} {
    margin-right: $i + px;
  }
  .mb-#{$i} {
    margin-bottom: $i + px;
  }
  .ml-#{$i} {
    margin-left: $i + px;
  }
  .m-#{$i}p {
    margin: $i + unquote("%");
  }
  .mt-#{$i}p {
    margin-top: $i + unquote("%");
  }
  .mr-#{$i}p {
    margin-right: $i + unquote("%");
  }
  .mb-#{$i}p {
    margin-bottom: $i + unquote("%");
  }
  .ml-#{$i}p {
    margin-left: $i + unquote("%");
  }

  .p-#{$i} {
    padding: $i + px;
  }
  .pt-#{$i} {
    padding-top: $i + px;
  }
  .pr-#{$i} {
    padding-right: $i + px;
  }
  .pb-#{$i} {
    padding-bottom: $i + px;
  }
  .pl-#{$i} {
    padding-left: $i + px;
  }
  .p-#{$i}p {
    padding: $i + unquote("%");
  }
  .pt-#{$i}p {
    padding-top: $i + unquote("%");
  }
  .pr-#{$i}p {
    padding-right: $i + unquote("%");
  }
  .pb-#{$i}p {
    padding-bottom: $i + unquote("%");
  }
  .pl-#{$i}p {
    padding-left: $i + unquote("%");
  }
  $i: $i + 1;
}

