.pagination{
text-align: center;
    margin-bottom: 1em;
}

.pagination li{
  display: inline-block;
  padding: 0;
  width: 2.5em;
  height: 2.5em;
  line-height: 2.5em;
  text-align: center;
  margin: 0 4px 8px 4px;
  background-color: #ffffff;
  border: 1px solid #e8e8e8;
  @include transition( all 180ms ease-out );
}
.pagination li:hover {
  background-color: #e8e8e8
}
.pagination li.active {
  background-color: #222a2d;
    border: 1px solid #222a2d;
}
.pagination li.active:hover {
  background-color: #191e21;
  border: 1px solid #191e21
}
.pagination li.disabled {
  background-color: #f8f8f8;
  border: 1px solid #e8e8e8;
  cursor: no-drop;
}
.pagination.double li{
  width: 5em;
}

.pagination li a,
.pagination li span{
      display: block;
    padding: 0;
    font-size: 1em;
    line-height: 2.5em;
    text-align: center;
    @include transition( all 180ms ease-out );

}
.pagination li.active a, 
.pagination li.active span{
  color:#ffffff;
}
.pagination li.disabled a,
.pagination li.disabled span {
  color: #999999;
}
.pagination > .pagination-prev,
.pagination > .pagination-next{
  padding:0 15px;
  position: relative;
}
.pagination > .pagination-prev > .page-prev,
.pagination > .pagination-next > .page-next{
  display: inline-block;
  width: 1em;
}
.pagination > .pagination-prev > .page-prev:before{
  content: "";
  top: 50%;
  left: 17px;
  width: 12px;
  height: 12px;
  margin: -6px 0 0 0;
  position: absolute;
  transform: rotate(-45deg);
  border-left: 1px solid #FFFFFF;
  border-top: 1px solid #FFFFFF;
}
.pagination > .pagination-next > .page-next:before{
  content: "";
  top: 50%;
  right: 17px;
  width: 12px;
  height: 12px;
  margin: -6px 0 0 0;
  position: absolute;
  transform: rotate(45deg);
  border-top: 1px solid #FFFFFF;
  border-right: 1px solid #FFFFFF;
  
}

