$i: 1;
@while $i <= 100 {
  .border-width-#{$i} {
    border-width: $i+px;
  }
  $i: $i + 1;
}

.aione-shadow{
    @include shadow();
}
.aione-shadow.shadow-small{
    @include shadow( 0, 0, 1em, -0.2em, rgba(0,0,0,.25));
}
.aione-shadow.shadow-medium{
    @include shadow( 0, 0, 2em, -0.3em, rgba(0,0,0,.3));
}
.aione-shadow.shadow-large{
    @include shadow( 0, 0, 3em, -0.4em, rgba(0,0,0,.35));
}
.aione-shadow.shadow-xl{
    @include shadow( 0, 0, 4em, -0.5em, rgba(0,0,0,.4));
}
