/***********************************
Aione Typography
  Aione List
***********************************/

html{
  color:#525a5f; 
  line-height: 1.6;
  font-weight: normal;
  letter-spacing: 0.8px;
  font-family: Arial, Helvetica, sans-serif;

  @media only screen and (min-width: 0) {
    font-size: 16px;
  }

  @media only screen and (min-width: $medium-screen) {
    font-size: 17px;
  }

  @media only screen and (min-width: $large-screen) {
    font-size: 17.6px;
  }

}
body {
  line-height: 1.6; 
}

a{
  color:#095e86;
  text-decoration: none;
  @include transition( all 180ms ease-in-out );
}
a:hover{
  color:#064f72;
}


p {
  font-size: 1em;
  line-height: 1.6em;
  font-weight: normal;
  margin-bottom: 2em;
}
p:empty {
  display: none;
  margin-bottom: 0;
}


/* Text-level semantics
   ========================================================================== */

/**
 * Address styling not present in IE 8/9/10/11, Safari, and Chrome.
 */

abbr[title] {
  border-bottom: 1px dotted;
}

/**
 * Address style set to `bolder` in Firefox 4+, Safari, and Chrome.
 */

b,
strong {
  font-weight: bold;
}

/**
 * Address styling not present in Safari and Chrome.
 */

dfn {
  font-style: italic;
}

/**
 * Address styling not present in IE 8/9.
 */

mark {
  background: #ff0;
  color: #000;
}

/**
 * Prevent `sub` and `sup` affecting `line-height` in all browsers.
 */

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

// Heading Styles
h1, 
h2, 
h3, 
h4, 
h5, 
h6 {
  line-height: 1.6em;
  font-weight: normal;
}

h1 a, 
h2 a, 
h3 a, 
h4 a, 
h5 a, 
h6 a,
h1 span, 
h2 span, 
h3 span, 
h4 span, 
h5 span, 
h6 span { 
  font-size: inherit; 
  font-family: inherit; 
  font-weight: inherit; 
}

/*
h1 2.625em;   42px 
h2 2.25em;    36px  
h3 2em;       32px
h4 1.5em;     24px
h5 1.17em;    18px
h6 1em;       16px

*/

h1 { font-size: 2.625em; margin:0 0 .67em 0;}
h2 { font-size: 2.25em; margin:0 0 .75em 0;}
h3 { font-size: 2em; margin:0 0 .83em 0;}
h4 { font-size: 1.5em; margin:0 0 1em 0;}
h5 { font-size: 1.17em; margin: 0 0 1.5em 0;}
h6 { font-size: 1em; margin: 0 0 1.67em 0;}

h5, 
h6{ 
  font-weight: bolder 
}

// Text Styles
em { font-style: italic; }
strong { font-weight: 500; }
small { font-size: 75%; }
.light { font-weight: 300; }
.thin { font-weight: 200; }

.font-weight-100{ font-weight: 100; }
.font-weight-200{ font-weight: 200; }
.font-weight-300{ font-weight: 300; }
.font-weight-400{ font-weight: 400; }
.font-weight-500{ font-weight: 500; }
.font-weight-600{ font-weight: 600; }
.font-weight-700{ font-weight: 700; }
.font-weight-800{ font-weight: 800; }
.font-weight-900{ font-weight: 900; }

$i: 1;
@while $i <= 100 {
  .font-size-#{$i} {
    font-size: $i+px;
  }
  $i: $i + 1;
}
$i: 110;
@while $i <= 200 {
  .font-size-#{$i} {
    font-size: $i+px;
  }
  $i: $i + 10;
}

$i: 1;
@while $i <= 100 {
  .line-height-#{$i} {
    line-height: $i+px;
  }
  $i: $i + 1;
}

/***********************************
Aione List
***********************************/

.aione-list{
  margin-bottom: 1.25em;
  font-size: 1.1em;
  line-height: 2em;
}
.aione-list.small{
  font-size: 1em;
  line-height: 1.375em;
}
.aione-list.medium{
  font-size: 1.17em;
  line-height: 1.875em;
}
.aione-list.large{
  font-size: 1.5em;
  line-height: 2em;
}

.aione-list li{
  padding-left: 2em;
  position: relative;
}
.aione-list.medium li{
  padding-left: 2.25em;
}
.aione-list.large li{
  padding-left: 2.5em;
}
.aione-list li:before{
  left: 0;
    content: "\f28a";
    font-weight: inherit;
    line-height: 1.5em;
    speak: none;
    text-align: center;
    position: absolute;
    display: inline-block;
    font-family: Ionicons;
    font-style: normal;
    font-variant: normal;
    text-transform: none;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
}
.aione-list li a{

}
.aione-list li:before{
    width: 2em;
    line-height: 1.5em;
}
.aione-list.medium li:before{
    width: 2.25em;
    line-height: 1.875em;
}
.aione-list.large li:before{
    width: 2.5em;
    line-height: 2em;
}
.aione-list li .aione-icon{
    margin-right:10px;
    font-size:24px;
    line-height:24px;
    margin-top:4px;
    display:inline-block;
}
.aione-list.small li .aione-icon{
    margin-right:6px;
    font-size:18px;
    line-height:20px;
    margin-top:0px;
}
.aione-list.medium li .aione-icon{
    margin-right:6px;
    font-size:24px;
    line-height:30px;
}

.aione-list.large li .aione-icon{
    margin-right:15px;
    font-size:30px;
    line-height:30px;
}
/********************************
Theme Default
*********************************/
.aione-list.theme-default li:before{
  content:"";
  top: 1em;
  height:1px;
  width: 1.7em;
  background-color: #909090;
}
.aione-list.small.theme-default li:before{
  top: .6875em;
  width: 1.7em;
}
.aione-list.medium.theme-default li:before{
  top: .9375em;
  width: 2em;
}
.aione-list.large.theme-default li:before{
  top: 1em;
  width:2.3em;
}
/********************************
Theme Solid
*********************************/
.aione-list.theme-solid li{
  border-bottom:1px solid #e8e8e8;
}
/********************************
Theme Dotted
*********************************/
.aione-list.theme-dotted li{
  border-bottom:1px dotted #e8e8e8;
}

/********************************
Theme Tree
*********************************/
ul.aione-list.theme-tree,
.aione-list.theme-tree ul{
  position: relative;
}
.aione-list.theme-tree li{

}
.aione-list.theme-tree li:after{
    content: "";
    left: 0;
    top: 1em;
    margin: 0;
    width: 1.7em;
    position: absolute;
    border-bottom: 1px solid #d2d2d2;
}
.aione-list.theme-tree li:before{
  content: "";
  left: 0;
  top: 0;
  height: 100%;
  position: absolute;
  display: inline-block;
  border-left: 1px solid #d2d2d2;
}
.aione-list.theme-tree li:last-child:before{
  height: 1em;
}
.aione-list.small.theme-tree li:after{
  top: 0.6875em;
  width:1.7em;
}
.aione-list.medium.theme-tree li:after{
  top: 0.9375em;
  width:2em;
}
.aione-list.large.theme-tree li:after{
  top: 1em;
  width:2.3em;
}
.aione-list.small.theme-tree li:last-child:before{
  height: 0.6875em;
}
.aione-list.medium.theme-tree li:last-child:before{
  height: 0.9375em;
}
.aione-list.large.theme-tree li:last-child:before{
  height: 1em;
}

/********************************
Theme Checkmark
*********************************/
ul.aione-list.theme-checkmark,
.aione-list.theme-checkmark ul{
  position: relative;
}
.aione-list.theme-checkmark li:before{
  content: "";
  top: 0.5em;
  left: 0.5em;
  width: 1em;
  height: 1em;
  position: absolute;
  display: inline-block;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(../images/elements/aione-list-icon-1.png);
}
.aione-list.small.theme-checkmark li{
  padding-left: 1.5em;
}
.aione-list.medium.theme-checkmark li{
  padding-left: 2em;
}
.aione-list.large.theme-checkmark li{
  padding-left: 2em;
}
.aione-list.small.theme-checkmark li:before{
  left: 0.25em;
  top: 0.1875em;
}
.aione-list.medium.theme-checkmark li:before{
  left: 0.5em;
  top: 0.4375em;
}
.aione-list.large.theme-checkmark li:before{
}



/********************************
Theme Solid
*********************************/
.aione-list.theme-solid > li{
  padding:15px;
}